    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif; /* Default font modern */
}

body,
html {
    width: 100%;
    height: 100%;
    background-color: #eef2f3;
}

body {
    font-size: 14px;
    color: #333;
}

/* Mobile Navbar */
.mobile-navbar {
    background-color: #1a73e8;
    color: #ffffff;
    width: 100%;
    display: flex;
    font-weight: 600;
    font-size: 20px;
    padding: 10px;
    justify-content: space-between;
    font-family: 'Roboto', sans-serif; /* Gunakan Roboto untuk menu mobile */
}

.menu-icon a {
    text-decoration: none;
    color: #ffffff;
}

.mobile-nav,
.mobile-nav a {
    font-family: 'Quicksand', sans-serif; /* Menu link menggunakan Quicksand */
}

.mobile-nav-list {
    width: 60%;
    padding: 0;
    margin: 0;
    text-align: left;
}

.mobile-nav-item {
    display: inline-block;
    text-align: right;
    line-height: 24px;
    text-transform: uppercase;
    color: black !important;
}

.mobile-nav a {
    display: inline-block;
    padding: 1.3em;
    text-decoration: none;
    color: black !important;
    min-width: 44px; /* Menambah dimensi sentuh */
    min-height: 44px; /* Menambah dimensi sentuh */
    text-align: center; /* Menyelaraskan teks di tengah */
}

.mobile-nav.open {
    display: block;
}

/* Desktop Navbar */
.desktop-nav {
    background: linear-gradient(90deg, #1a73e8, #4caf50);
    padding: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.desktop-nav-list {
    width: 80%;
    padding: 0;
    margin: 0;
    text-align: right;
}

.desktop-logo {
    font-family: 'Playfair Display', serif;
    font-size: 24px;
    font-weight: 700;
}

.desktop-nav-item {
    display: inline-block;
    text-align: right;
    line-height: 24px;
    text-transform: uppercase;
}

.desktop-nav a {
    color: white;
    text-decoration: none;
    font-weight: 500;
    margin-left: 16px;
    transition: color 0.3s;
    padding: 12px 20px; /* Menambah padding untuk mematuhi dimensi sentuh */
    min-width: 44px; /* Menambah dimensi sentuh */
    min-height: 44px; /* Menambah dimensi sentuh */
}

.desktop-nav a:hover {
    color: #ffcc00;
}

/* Hero Section */
.hero-section {
    display: flex;
    align-items: center;
    min-height: 400px;
    width: 100%;
    text-align: center;
    background-color: #4caf50;
    background-position: center;
    color: #ffffff;
    background-size: cover;
    font-family: 'Poppins', sans-serif;
}

.hero-inner {
    margin: 0 auto;
    max-width: 800px;
}

.hero-title {
    font-family: 'Roboto', sans-serif; /* Gunakan Roboto untuk judul */
    font-weight: 700;
    font-size: 38px;
    text-shadow: 2px 2px #2c2c2c;
}

.hero-subtitle {
    font-family: 'Quicksand', sans-serif;
}

/* Main Content */
main {
    width: 100%;
    margin: 0 auto;
}

.main-content {
    padding: 40px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
    font-family: 'Roboto', sans-serif; /* Gunakan Roboto untuk konten */
}

.latest-explore {
    width: 100%;
    margin-top: 40px;
    margin-bottom: 60px;
    text-align: center;
}

.latest-explore h1 {
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    font-size: 2.2em;
    margin-bottom: 40px;
    color: #1a73e8;
}

/* Grid Layout untuk List */
.restaurant-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    grid-gap: 20px;
    margin: 40px auto;
    text-align: left;
}

.list_item {
  position: relative;
  box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  overflow: hidden;
  background-color: #ffffff;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
}

.list_item:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.25);
}

.city {
  position: absolute; /* Posisi absolut untuk menempatkan di atas gambar */
  top: 10px; /* Margin dari atas */
  left: 10px; /* Margin dari kiri */
  background-color: #ff5722; /* Warna oranye */
  padding: 5px 12px;
  color: #ffffff;
  font-weight: 500;
  border-radius: 5px;
  z-index: 2; /* Menjamin elemen city muncul di atas elemen lain */
}

.list_item_thumb {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
}

.list_item_content {
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
}

.list_item_title {
  font-weight: 600;
  font-size: 18px;
  margin-top: 10px;
  color: #333;
  transition: 0.3s opacity;
}

.list_item_title a {
  text-decoration: none;
  color: #3b9eff; /* Biru cerah */
}

.list_item_title:hover {
  opacity: 0.6;
}

.list_item_rating {
  font-size: 14px;
  font-weight: 600;
  color: #666;
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.list_item_rating_value {
  color: #e91e63; /* Warna pink cerah */
  margin-left: 5px;
}

/* Footer */
.site-footer {
    background-color: #1a73e8;
    color: white;
    padding: 20px;
    text-align: center;
    font-size: 14px;
    margin-top: 40px;
    font-family: 'Inter', sans-serif;
}

.site-footer a {
    color: #ffcc00;
    text-decoration: none;
}

.site-footer a:hover {
    text-decoration: underline;
}


/*
  Like
*/

.like {
    font-size: 18px;
    position: fixed;
    bottom: 16px;
    right: 16px;
    background-color: #db0000;
    color: white;
    border: 0;
    border-radius: 50%;
    width: 55px;
    height: 55px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }

/* Skip Link for Accessibility */
.skip-link {
    position: absolute;
    top: -100px;
    left: 0;
    background-color: #e91e63;
    color: #ffffff;
    padding: 10px;
    font-size: 20px;
    z-index: 100;
    text-decoration: none;
}

.skip-link:focus {
    top: 100px;
}

a, button, input, textarea {
    display: inline-block;
    min-width: 44px;
    min-height: 44px;
  }`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,gCAAgC,EAAE,wBAAwB;AAC9D;;AAEA;;IAEI,WAAW;IACX,YAAY;IACZ,yBAAyB;AAC7B;;AAEA;IACI,eAAe;IACf,WAAW;AACf;;AAEA,kBAAkB;AAClB;IACI,yBAAyB;IACzB,cAAc;IACd,WAAW;IACX,aAAa;IACb,gBAAgB;IAChB,eAAe;IACf,aAAa;IACb,8BAA8B;IAC9B,iCAAiC,EAAE,qCAAqC;AAC5E;;AAEA;IACI,qBAAqB;IACrB,cAAc;AAClB;;AAEA;;IAEI,oCAAoC,EAAE,oCAAoC;AAC9E;;AAEA;IACI,UAAU;IACV,UAAU;IACV,SAAS;IACT,gBAAgB;AACpB;;AAEA;IACI,qBAAqB;IACrB,iBAAiB;IACjB,iBAAiB;IACjB,yBAAyB;IACzB,uBAAuB;AAC3B;;AAEA;IACI,qBAAqB;IACrB,cAAc;IACd,qBAAqB;IACrB,uBAAuB;IACvB,eAAe,EAAE,4BAA4B;IAC7C,gBAAgB,EAAE,4BAA4B;IAC9C,kBAAkB,EAAE,iCAAiC;AACzD;;AAEA;IACI,cAAc;AAClB;;AAEA,mBAAmB;AACnB;IACI,oDAAoD;IACpD,aAAa;IACb,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,WAAW;IACX,yCAAyC;AAC7C;;AAEA;IACI,UAAU;IACV,UAAU;IACV,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,sCAAsC;IACtC,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,qBAAqB;IACrB,iBAAiB;IACjB,iBAAiB;IACjB,yBAAyB;AAC7B;;AAEA;IACI,YAAY;IACZ,qBAAqB;IACrB,gBAAgB;IAChB,iBAAiB;IACjB,sBAAsB;IACtB,kBAAkB,EAAE,mDAAmD;IACvE,eAAe,EAAE,4BAA4B;IAC7C,gBAAgB,EAAE,4BAA4B;AAClD;;AAEA;IACI,cAAc;AAClB;;AAEA,iBAAiB;AACjB;IACI,aAAa;IACb,mBAAmB;IACnB,iBAAiB;IACjB,WAAW;IACX,kBAAkB;IAClB,yBAAyB;IACzB,2BAA2B;IAC3B,cAAc;IACd,sBAAsB;IACtB,kCAAkC;AACtC;;AAEA;IACI,cAAc;IACd,gBAAgB;AACpB;;AAEA;IACI,iCAAiC,EAAE,+BAA+B;IAClE,gBAAgB;IAChB,eAAe;IACf,4BAA4B;AAChC;;AAEA;IACI,oCAAoC;AACxC;;AAEA,iBAAiB;AACjB;IACI,WAAW;IACX,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,kBAAkB;IAClB,yCAAyC;IACzC,iCAAiC,EAAE,gCAAgC;AACvE;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,kCAAkC;IAClC,gBAAgB;IAChB,gBAAgB;IAChB,mBAAmB;IACnB,cAAc;AAClB;;AAEA,2BAA2B;AAC3B;IACI,aAAa;IACb,2DAA2D;IAC3D,cAAc;IACd,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;EACE,kBAAkB;EAClB,4CAA4C;EAC5C,kBAAkB;EAClB,gBAAgB;EAChB,yBAAyB;EACzB,yBAAyB;EACzB,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,0CAA0C;AAC5C;;AAEA;EACE,kBAAkB,EAAE,oDAAoD;EACxE,SAAS,EAAE,qBAAqB;EAChC,UAAU,EAAE,qBAAq